/* eslint-disable react-hooks/exhaustive-deps */
import {
  Table,
  Button,
  Form,
  Row,
  Col,
  Popconfirm,
  Modal,
  Spin,
  Typography,
  Select,
  Input,
  Tag,
  Divider,
  message,
  Checkbox,
  Card,
  Image,
} from "antd";
import {
  DeleteOutlined,
  EditOutlined,
  LoadingOutlined,
  PlusOutlined,
  CloseOutlined,
} from "@ant-design/icons";
import React, {
  useEffect,
  useState,
  forwardRef,
  useImperativeHandle,
  useRef,
  Fragment,
} from "react";
import Pagination from "../../components/Pagination";
import { AntdMsg } from "../../../utils/Antd";
import service from "../../../services/product";
import util from "../../../utils/util";
import Varient from "./Varient";
import UploadImage from "../../../utils/UploadImage";
import categoryService from "../../../services/category";
import specializationService from "../../../services/specialization";
import TinyMce from "../../../utils/TinyMce";
export default function Product({ productSpecialization, categoryId }) {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [selected, setSelected] = useState([]);
  const [category, setCategory] = useState([]);
  const [specializations, setSpecializations] = useState([]);
  const [qData, setQData] = useState({
    page: 1,
    limit: 20,
    type: "product",
    categoryId,
  });
  const addNewModalRef = useRef();
  const [ellipsis, setEllipsis] = useState(true);
  const columns = [
    {
      title: "#",
      width: 40,
      render: (v, row, i) => <b>{i + 1}</b>,
    },
    {
      title: "Name",
      dataIndex: "name",
      width: 150,
    },
    {
      title: "Slug",
      dataIndex: "slug",
      width: 100,
    },
    {
      title: "Image",
      dataIndex: "thumbImg",
      width: 100,
      render: (v) => {
        return <Image src={v?.url} height={80} />;
      },
    },
    {
      title: "Categories",
      dataIndex: "categoryDetails",
      width: 250,
      render: (v) =>
        v.map((vv) => <Tag key={vv._id}>{vv.name || "UNKNOWN"}</Tag>),
    },

    {
      title: "Status",
      dataIndex: "status",
      width: 80,
      render: (isActive) => {
        if (isActive) {
          return <Tag color="green">Active</Tag>;
        } else {
          return <Tag color="red">Inactive</Tag>;
        }
      },
    },
    {
      title: "Action",
      dataIndex: "_id",
      width: 70,
      render: (v, row) => {
        return (
          <>
            <Popconfirm
              title="Are you sure to delete this data?"
              onConfirm={() => {
                deleteData(row);
              }}
              okText="Yes"
              cancelText="No"
            >
              <Button
                type="primary"
                size="small"
                danger
                icon={<DeleteOutlined />}
              />
              &nbsp;
            </Popconfirm>
            <Button
              type="primary"
              size="small"
              icon={<EditOutlined />}
              onClick={() => {
                addNewModalRef.current.openForm(row);
              }}
            />
          </>
        );
      },
    },
  ].filter((item) => !item.hidden);

  function list() {
    setLoading(true);
    if (qData.categoryId !== "") {
      service
        .listUser(qData)
        .then((res) => {
          setData(res.data?.map((v) => ({ ...v, key: v._id })));
          setQData({
            ...qData,
            limit: res.extra.limit,
            page: res.extra.page,
            total: res.extra.total,
          });
        })
        .catch((err) => {})
        .finally(() => {
          setLoading(false);
        });
    }
  }

  const deleteData = (id) => {
    service
      .deleteUser(id)
      .then((res) => {
        AntdMsg(res.message);
        list();
        setSelected([]);
      })
      .catch((err) => {
        AntdMsg(err.message, "error");
      });
  };
  const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));
  tableColumns[0].fixed = true;
  tableColumns[tableColumns.length - 1].fixed = "right";
  const tableProps = {
    bordered: true,
    loading,
    size: "small",
    title: () => (
      <Search
        {...{ addNewModalRef, selected, deleteData, qData, setQData, list }}
      />
    ),
    showHeader: true,
    footer: () => <Pagination {...{ qData, setQData }} />,
    rowSelection: {
      onChange: (selectedRowKeys) => {
        setSelected(selectedRowKeys);
      },
    },
    tableLayout: undefined,
  };
  useEffect(() => {
    list();
  }, [qData.page, qData.limit, qData.categoryId, categoryId]);

  useEffect(() => {
    categoryService
      .list({ isAll: 1 })
      .then((res) => {
        setCategory(res.data || []);
      })
      .catch((err) => {
        message.error("Category data not loaded");
      });
    specializationService
      .typeList()
      .then((res) => {
        setSpecializations(res.data || []);
      })
      .catch((err) => {
        message.error("Specialization data not loaded");
      });
  }, []);

  return (
    <>
      <Typography.Title level={5} style={{ marginTop: 0 }}>
        Product List
      </Typography.Title>
      <Table
        {...tableProps}
        pagination={{ position: ["none"], pageSize: qData.limit }}
        columns={tableColumns}
        dataSource={data.length ? data : []}
        scroll={{ y: "calc(100vh - 340px)", x: "calc(100vw - 387px)" }}
        expandable={{
          rowExpandable: (record) => true,
          expandedRowRender: (record) => (
            <>
              <div style={{ display: "flex", flexDirection: "row-reverse" }}>
                <Table
                  // width={300}
                  bordered
                  // style={{width: '500px'}}
                  scroll={{
                    y: "calc(100vh - 340px)",
                    x: "calc(100vw - 387px)",
                  }}
                  pagination={false}
                  dataSource={[record]}
                  columns={[
                    {
                      title: "Images",
                      dataIndex: "imgs",
                      width: 300,
                      render: (v) => (
                        <>
                            {v.map(vv => <Image src={vv?.url} height={80} />)}
                        </>
                      ),
                    },
                    {
                      title: "All Variants",
                      dataIndex: "variants",
                      width: 600,
                      render: (v) => (
                        <>
                          <div>
                            <span style={{ margin: "0 10px" }}>No.</span>
                            <span style={{ margin: "0 10px" }}>Name.</span>
                            <span style={{ margin: "0 10px" }}>
                              Price(In ₹)
                            </span>
                            <span style={{ margin: "0 10px" }}>
                              actual Price (In ₹)
                            </span>
                            <span style={{ margin: "0 10px" }}>Inventory</span>
                          </div>
                          {v.map((vv, i) => (
                            <div>
                              <div>
                                <span style={{ margin: "0 10px" }}>
                                  {i + 1}.
                                </span>
                                <span style={{ margin: "0 30px" }}>
                                  {vv.weight}
                                </span>
                                <span style={{ margin: "0 10px" }}>
                                  {vv.price}
                                </span>
                                <span style={{ margin: "0 70px" }}>
                                  {vv.actualPrice}
                                </span>
                                <span style={{ margin: "0 10px" }}>
                                  {vv.inventory}
                                </span>
                              </div>
                              <hr />
                            </div>
                          ))}
                        </>
                      ),
                    },
                  ]}
                />
              </div>
            </>
          ),
        }}
      />
      <AddForm
        ref={addNewModalRef}
        {...{
          list,
          category,
          qData,
          specializations,
          productSpecialization,
          categoryId,
        }}
      />
    </>
  );
}

function Search({
  addNewModalRef,
  selected,
  deleteData,
  qData,
  setQData,
  list,
}) {
  const onChange = (v, key) => {
    qData[key] = v;
    if (v === undefined || v === "") {
      delete qData[key];
      list();
    }
  };
  return (
    <Form onSubmitCapture={list} className="search-form">
      <Row gutter={[12, 2]}>
        <Col xs={24} xl={4}>
          <Form.Item style={{ marginBottom: 0 }}>
            <Input
              placeholder="Search by name or code"
              value={qData.key}
              onChange={(e) => onChange(e.target.value, "key")}
              allowClear
            />
          </Form.Item>
        </Col>
        <Col span={3}>
          <Form.Item style={{ marginBottom: 0 }}>
            <Button type="primary" htmlType="submit">
              Search
            </Button>
          </Form.Item>
        </Col>
        <Col span={5}>
          <Form.Item style={{ marginBottom: 0, float: "right" }}>
            <Button
              type="primary"
              icon={<PlusOutlined />}
              onClick={() => {
                addNewModalRef.current.openForm();
              }}
            >
              Add New
            </Button>
          </Form.Item>
          {selected.length ? (
            <Popconfirm
              title="Are you sure to delete these selected data ?"
              onConfirm={() => {
                deleteData(selected);
              }}
              okText="Yes"
              cancelText="No"
            >
              <Button
                type="primary"
                danger
                style={{ float: "right" }}
                icon={<DeleteOutlined />}
              >
                Delete Selected
              </Button>
            </Popconfirm>
          ) : null}
        </Col>
      </Row>
    </Form>
  );
}

const AddForm = forwardRef((props, ref) => {
  const { list, categoryId, category, specializations, productSpecialization } =
    props;

  const [open, setOpen] = useState(false);
  const [data, setData] = useState({ aspect: 1 });
  const [ajxRequesting, setAjxRequesting] = useState(false);
  const [thumbFile, setThumbFile] = useState([]);
  const [file, setFile] = useState([]);

  const handleChange = (v, k) => {
    let varDt = data;
    let keys = k.split(".");
    for (let i = 0; i < keys.length; i++) {
      if (i + 1 === keys.length) {
        varDt[keys[i]] = v;
      } else {
        if (typeof varDt[keys[i]] === "undefined") {
          if (parseInt(keys[i + 1]) * 1 >= 0) {
            varDt[keys[i]] = [];
          } else {
            varDt[keys[i]] = {};
          }
        }
        varDt = varDt[keys[i]];
      }
    }
    setData({ ...data, ...v });
  };

  useImperativeHandle(ref, () => ({
    openForm(dt) {
      setOpen(true);
      setData(
        dt
          ? {
              ...dt,
              ocassion: [],
              flavour: [],
              recipient: [],
              relation: [],
              specializationIds: [],
            }
          : {
              isPremium: false,
              isBestSelling: false,
              forBirthday: false,
              forAnniversary: false,
              isBestSellingCombos: false,
              variants: [{}],
              ocassion: [],
              flavour: [],
              recipient: [],
              relation: [],
              specializationIds: [],
              categoryIds: [categoryId],
            }
      );
    },
  }));

  const save = () => {
    setAjxRequesting(true);
    service
      .saveUser(data)
      .then((res) => {
        message.success(res.message);
        list();
        setOpen(false);
      })
      .catch((err) => {
        if (typeof err.message === "object") {
          let dt = err.message[Object.keys(err.message)[0]];
          message.error(dt);
        } else {
          AntdMsg(err.message, "error");
        }
      })
      .finally(() => {
        setAjxRequesting(false);
      });
  };

  useEffect(() => {
    if (!data._id) {
      handleChange(util.removeSpecialChars(data?.name || ""), "slug");
    }
  }, [data.name]);

  useEffect(() => {
    setData({ ...data, thumbImg: thumbFile[0] });
  }, [thumbFile]);

  useEffect(() => {
    setData({ ...data, imgs: file });
  }, [file]);

  useEffect(() => {
    if (
      data?.ocassion?.length > 0 ||
      data?.flavour?.length > 0 ||
      data?.recipient?.length > 0 ||
      data?.relation?.length > 0
    ) {
      handleChange(
        [
          ...data?.ocassion,
          ...data?.flavour,
          ...data?.relation,
          ...data?.recipient,
        ],
        "specializationIds"
      );
    }
  }, [data.ocassion, data.flavour, data?.relation, data?.recipient]);

  return (
    <>
      <Modal
        title={(!data._id ? "Add New" : "Edit") + " Product"}
        style={{ top: 20 }}
        open={open}
        okText="Save"
        onOk={save}
        okButtonProps={{ disabled: ajxRequesting }}
        onCancel={() => {
          setOpen(false);
        }}
        destroyOnClose
        maskClosable={false}
        width={1200}
        className="app-modal-body"
        footer={[
          <Button
            key="cancel"
            onClick={() => {
              setOpen(false);
            }}
          >
            Cancel
          </Button>,
          <Button key="save" type="primary" onClick={save}>
            Save
          </Button>,
        ]}
      >
        <Spin
          spinning={ajxRequesting}
          indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}
        >
          <Form layout="vertical">
            <Row gutter={[12, 2]}>
              <Col span={8}>
                <Form.Item label="Name" required>
                  <Input
                    placeholder="Name"
                    value={data.name}
                    onChange={(e) => {
                      handleChange(e.target.value, "name");
                    }}
                  />
                </Form.Item>
              </Col>
              <Col span={8}>
                <Form.Item label="Slug" required>
                  <Input
                    placeholder="Slug"
                    value={data.slug}
                    onChange={(e) => {
                      handleChange(
                        util.removeSpecialChars(e.target.value),
                        "slug"
                      );
                    }}
                  />
                </Form.Item>
              </Col>
              <Col span={8}>
                <Form.Item label="Category" required>
                  <Select
                    mode="multiple"
                    value={data.categoryIds}
                    onChange={(v) => {
                      handleChange(v, "categoryIds");
                    }}
                    options={category.map((v) => ({
                      value: v._id,
                      label: v.name,
                    }))}
                  ></Select>
                </Form.Item>
              </Col>
              {productSpecialization?.map((v, i) => (
                <Col span={12} key={i}>
                  <Form.Item label={`Specialization (${v?.toUpperCase()})`}>
                    <Select
                      mode="multiple"
                      value={data?.[v]}
                      onChange={(vv) => {
                        handleChange(vv, v);
                      }}
                      options={specializations
                        .filter((vv) => vv.type === v)
                        .map((vv, ii) => ({
                          value: vv._id,
                          label: vv.name,
                        }))}
                    ></Select>
                  </Form.Item>
                </Col>
              ))}

              <Col span={24}>
                <Variant
                  {...{
                    variantDetails: data.variants,
                    parentKay: "variants",
                    handleChange,
                  }}
                />
              </Col>
              <Col span={24}>
                <Form.Item label="Description" required>
                  <TinyMce
                    initialValue={data.description}
                    onChange={(v) => {
                      data.description = v;
                    }}
                  />
                </Form.Item>
              </Col>
              <Col span={24}>
                <Form.Item label="High Light" required>
                  <TinyMce
                    initialValue={data.heighlight}
                    onChange={(v) => {
                      data.heighlight = v;
                    }}
                  />
                </Form.Item>
              </Col>
              <Col span={8}>
                <Form.Item label="Meta Title" required>
                  <Input
                    placeholder="Meta title"
                    value={data.metaTitle}
                    onChange={(e) => {
                      handleChange(e.target.value, "metaTitle");
                    }}
                  />
                </Form.Item>
              </Col>
              <Col span={8}>
                <Form.Item label="Meta Tag" required>
                  <Input
                    placeholder="Meta tag"
                    value={data.metaTag}
                    onChange={(e) => {
                      handleChange(e.target.value, "metaTag");
                    }}
                  />
                </Form.Item>
              </Col>
              <Col span={8}>
                <Form.Item label="Meta Description" required>
                  <Input.TextArea
                    rows={1}
                    placeholder="Meta-Description"
                    value={data.metaDescription}
                    onChange={(e) => {
                      handleChange(e.target.value, "metaDescription");
                    }}
                  />
                </Form.Item>
              </Col>
              <Divider />
              <Col span={8}>
                <Form.Item>
                  <Card bordered size="small">
                    <Checkbox
                      value={data.isPopular}
                      onChange={(v) => handleChange({ isPopular: v })}
                    >
                      Popular
                    </Checkbox>
                  </Card>
                </Form.Item>
              </Col>
              <Col span={8}>
                <Form.Item>
                  <Card bordered size="small">
                    <Checkbox
                      value={data.isPremium}
                      onChange={(v) => handleChange({ isPremium: v })}
                    >
                      Premium
                    </Checkbox>
                  </Card>
                </Form.Item>
              </Col>
              <Col span={8}>
                <Form.Item>
                  <Card bordered size="small">
                    <Checkbox
                      value={data.isBestSelling}
                      onChange={(v) => handleChange({ isBestSelling: v })}
                    >
                      Best Selling
                    </Checkbox>
                  </Card>
                </Form.Item>
              </Col>
              <Col span={6}>
                <Form.Item label="Status" required>
                  <Select
                    value={data.status}
                    onChange={(e) => {
                      handleChange(e, "status");
                    }}
                  >
                    <Select.Option value={true}>Active</Select.Option>
                    <Select.Option value={false}>Inactive</Select.Option>
                  </Select>
                </Form.Item>
              </Col>

              <Divider />
              <Col span={4}>
                <Form.Item label="Thumbnail Photo">
                  <UploadImage
                    {...{ fileList: thumbFile, setFileList: setThumbFile }}
                  />
                </Form.Item>
              </Col>
              <Col span={20}>
                <Form.Item label="Photo">
                  <UploadImage
                    {...{ fileList: file, setFileList: setFile, count: 20 }}
                  />
                </Form.Item>
              </Col>
            </Row>
          </Form>
        </Spin>
      </Modal>
    </>
  );
});

function Variant({ variantDetails: data, parentKay, handleChange }) {
  return (
    <Fragment>
      <Divider orientation="left">
        {parentKay === "variants" ? "Variants" : "Variant"}
      </Divider>
      <Table
        {...{
          bordered: true,
          size: "small",
          showHeader: true,
          footer: null,
          tableLayout: undefined,
        }}
        pagination={{ position: ["none"] }}
        columns={[
          {
            title: "Weight",
            dataIndex: "weight",
            // width: 150,
            render: (v, row, i) => (
              <Input
                placeholder="Weight"
                value={v}
                onChange={(e) => {
                  handleChange(e.target.value, `${parentKay}.${i}.weight`);
                }}
              />
            ),
          },
          {
            title: "Price",
            dataIndex: "price",
            width: 300,
            render: (v, row, i) => (
              <Input
                placeholder="Price (In ₹)"
                value={v}
                onChange={(e) => {
                  handleChange(
                    util.handleInteger(e.target.value),
                    `${parentKay}.${i}.price`
                  );
                }}
              />
            ),
          },
          {
            title: "Actual Price",
            dataIndex: "actual_price",
            width: 300,
            render: (v, row, i) => (
              <Input
                placeholder="Actual Price (In ₹)"
                value={v}
                onChange={(e) => {
                  handleChange(
                    util.handleFloat(e.target.value),
                    `${parentKay}.${i}.actualPrice`
                  );
                }}
              />
            ),
          },
          {
            title: "Inventory",
            dataIndex: "inventory",
            width: 300,
            render: (v, row, i) => (
              <Input
                placeholder="Inventory "
                value={v}
                onChange={(e) => {
                  handleChange(
                    util.handleFloat(e.target.value),
                    `${parentKay}.${i}.inventory`
                  );
                }}
              />
            ),
          },
          {
            title: "Action",
            dataIndex: "price",
            width: 100,
            render: (v, row, i) => {
              return (
                <>
                  {i + 1 === data.length ? (
                    <Button
                      icon={<PlusOutlined />}
                      shape="circle"
                      type="dashed"
                      onClick={() => {
                        handleChange([...data, {}], parentKay);
                      }}
                    />
                  ) : null}
                  {i + 1 === data.length && i !== 0 ? (
                    <Button
                      icon={<CloseOutlined />}
                      shape="circle"
                      type="dashed"
                      className="mx-2"
                      danger
                      onClick={() => {
                        data.pop();
                        handleChange([...data], parentKay);
                      }}
                    />
                  ) : null}
                </>
              );
            },
          },
        ]}
        dataSource={data?.length ? data?.map((v, i) => ({ ...v, key: i })) : []}
      />
    </Fragment>
  );
}
