/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Input, Row, Col, Popconfirm, Descriptions, Select, DatePicker, Typography, Tag } from "antd";
import { DeleteOutlined } from "@ant-design/icons";
import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from "react";
import service from "../../services/orders";
import Pagination from "../components/Pagination";
import { AntdMsg } from "../../utils/Antd";
import UserService from '../../services/userList';
import moment from 'moment'
import dayjs from 'dayjs';

export default function Order() {
    const [data, setData] = useState([]);
    const [user, setUser] = useState([]);
    const [loading, setLoading] = useState(false);
    const [qData, setQData] = useState({ page: 1, limit: 20,date : new Date(),status:'Confirmed' });

    const columns = [
        {
            title: "Order id",
            dataIndex: "orderId",
        },
        {
            title: "Shipping Address",
            dataIndex: "addressDetails",
            width: 400,
            render: (_) => {
                const data = JSON.parse(_);
                return data !== null ? <span>{data?.landMark + " " + data?.address + " " + data?.city + " " + data?.pin}</span> : <span>Not Mentioned</span>;
            },
        },
        {
            title: "Total Amount",
            dataIndex: "priceDetails",
            width: 90,
            render: (_) => {
                return <span>₹{_.finalPrice}</span>;
            },
        },
       
        {
            title: "OrderedAt",
            dataIndex: "createdAt",
            width: 180,
            render: (v) => {
                return moment(v).format("dddd, MMMM Do YYYY");
            }
        },
        {
            title: "Status",
            dataIndex: "status",
            width: 70,
            render: (_, { status }) => {
                return <Tag color="green">{status}</Tag>;
            },
        },
    ].filter((item) => !item.hidden);

    function list() {
        setLoading(true);
        service
            .list(qData)
            .then((res) => {
                setData(res.data?.map((v) => ({ ...v, key: v._id })));
                setQData({
                    ...qData,
                    limit: res.extra.limit,
                    page: res.extra.page,
                    total: res.extra.total,
                });
            })
            .catch((err) => { })
            .finally(() => {
                setLoading(false);
            });
    }

    function listUser() {
        UserService.listUser()
            .then(res => { setUser(res.data || []); }).catch(err => { AntdMsg('User data not loaded', 'error'); });
    }
    const deleteData = (id) => {
        service
            .delete(id)
            .then((res) => {
                AntdMsg(res.message);
                list();
            })
            .catch((err) => {
                AntdMsg(err.message, "error");
            });
    };

    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));

    tableColumns[0].fixed = true;
    tableColumns[tableColumns.length - 1].fixed = "right";

    const tableProps = {
        bordered: true,
        loading,
        size: "small",
        title: () => (
            <Search
                {...{ deleteData, qData, setQData, list }}
            />
        ),
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        tableLayout: undefined,
    };

    useEffect(() => {
        list();
        listUser()
    }, [qData.page, qData.limit]);

    return (
        <>

                <Typography.Title level={5}>List of Orders</Typography.Title>

            <Table
                {...tableProps}
                pagination={{ position: ["none"], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: "calc(100vh - 340px)", x: "calc(100vw - 387px)" }}
                expandable={{
                    expandedRowRender: (record) => (
                        
                        <Descriptions title="User Info">
                            <Descriptions.Item label="UserName">{record?.userDetails ? record?.userDetails.name : 'Not Accepted'}</Descriptions.Item>
                            <Descriptions.Item label="Email">{record?.userDetails ? record?.userDetails.email : 'Empty'}</Descriptions.Item>
                            <Descriptions.Item label="Phone">{record?.userDetails ? record?.userDetails.phone : 'Empty'}</Descriptions.Item>
                            <Descriptions.Item label="Gender">{record?.userDetails ? record?.userDetails.gender : "Empty"}</Descriptions.Item>
                            <Descriptions.Item label="Address">{
                                 record?.addressDetails !== null ?  <span>{JSON.parse(record?.addressDetails)?.landMark  + "" + JSON.parse(record?.addressDetails)?.address + " , " + JSON.parse(record?.addressDetails)?.city}</span>:<span>Not Mentioned</span>
                               
                            }</Descriptions.Item>
                        </Descriptions>
                    ),
                    rowExpandable: (record) => record.userDetails !== 'Not Expandable',
                }}
            />
        </>
    );
}

function Search({ deleteData, qData, setQData, list, user }) {
    const [userNames, setUserNames] = useState([]);

    const onChange = (v, key) => {
        qData[key] = v;
        if (v === undefined) {
            qData[key] = "";
            list()
        }
    }
    useEffect(() => {
        const Names = user?.map((item) => (
            {
                label: item.name ? <><p style={{ padding: "0px", margin: "0px" }}>{item.name}</p><p style={{ fontSize: "12px", margin: "0px" }}><Typography.Text type="danger">@{item.phone}</Typography.Text></p></> : item.phone,
                value: item._id,
                searchKey: item.name + item.phone
            }
        ))
        setUserNames(Names)
    }, [user])

    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                <Col span={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Input
                            placeholder="Search by order id"
                            value={qData.key}
                            onChange={e => onChange(e.target.value, "key")}
                            allowClear
                        />
                    </Form.Item>
                </Col>
                <Col xs={24} xl={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Select
                            showSearch
                            allowClear
                            placeholder="Select by username / phone"
                            optionFilterProp="children"
                            onChange={(e) => onChange(e, "userId")}
                            filterOption={(input, option) =>
                                (option?.searchKey ?? '').toLowerCase().includes(input.toLowerCase())
                            }
                            options={userNames}
                        />
                    </Form.Item>
                </Col>
                
                <Col xs={24} xl={4}>
                    <Form.Item>
                        <Select defaultValue={'New Order'} allowClear placeholder="Order Status" onChange={e => onChange(e, "status")} >
                            <Select.Option value="Confirmed">New Order</Select.Option>
                            <Select.Option value="Delivered">Delivered</Select.Option>
                            <Select.Option value="Failed">Failed</Select.Option>
                            <Select.Option value="Pending">Pending</Select.Option>
                            <Select.Option value="Shipped">Shipped</Select.Option>
                        </Select>
                    </Form.Item>
                </Col>
                <Col xs={24} xl={4}>
                    <Form.Item>
                        <DatePicker 
                        defaultValue={ dayjs(moment(qData?.date).format("YYYY-MM-DD"))}
                        allowClear onChange={e => onChange(e, "date")}/>
                    </Form.Item>
                </Col>
                <Col span={3}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Button type="primary" htmlType="submit">
                            Search
                        </Button>
                    </Form.Item>
                </Col>
            </Row>
        </Form>
    );
}