import React from "react";
import { Card, Layout, Menu } from "antd";
import { Routes, Route, Link } from "react-router-dom";
import Error404 from "../pages/errors/Error404";
import commonObj from "../../commonObj";

import Coupon from "../pages/Coupon";

import Application from "../pages/Application"
import Category from "../pages/Category";
import Banner from "../pages/Banner";
import UserManagement from "../pages/Management/userManagement";
import Product from "../pages/Product/Product";
import Offer from "../pages/Offer";
import AddOn from "../pages/Product/AddOn";
import HomePageItems from "../pages/HomePageItems";
import Order from "../pages/Order";
import Type from "../pages/Specialization/Type";
import Delivery from "../pages/delivery";
import Products from "../pages/Product";
import Settings from "../pages/Settings";
import Profile from "../pages/User/Profile";
import ChangePassword from "../pages/User/ChangePassword";
import DeliveryBoy from "../pages/DeliveryBoys";


export default function MyLayout() {

    return (
        <>
            <Layout style={{ minHeight: '100vh', }}>
                <Layout className="site-layout" >
                    <Layout.Header style={{ padding: 0, height: 'auto' }} >
                        <Menu theme="dark" mode="horizontal" items={[
                            {
                                label: <Link to={'/specialization'}>Specialization  </Link>,
                                key: "specialization",
                            },
                            {

                                label: <Link to={'/offer'}>Offers  </Link>,
                                key: "offer"
                            },
                            {
                                label: <Link to={'/coupon'}>Coupon</Link>,
                                key: "coupon"
                            },
                            {
                                label: <Link to={'/banner'}>Banner</Link>,
                                key: "banner"
                            },
                            {
                                label: <Link to={'/order'}>Order</Link>,
                                key: "order"
                            },
                            {
                                label: <Link to={'/addOn'}>AddOn</Link>,
                                key: "addOn"
                            },

                            {
                                label: <Link to={'/category'}>Category</Link>,
                                key: "category"
                            },

                            {
                                label: <Link to={'/application'}>Application</Link>,
                                key: "application"
                            },

                            {
                                label: <Link to={'/delivery'}>Delivery</Link>,
                                key: "delivery"
                            },
                            {
                                label: <Link to={'/deliveryBoy'}>Delivery-Boy</Link>,
                                key: "deliveryBoy"
                            },
                            {
                                label: <Link to={'/settings'}>Settings</Link>,
                                key: "settings"
                            },
                            {
                                label: <Link to={'/products'}>Products</Link>,
                                key: 'products',
                            },
                            {
                                label: <Link to={'/homepageitems'}>Home Page Items</Link>,
                                key: "homePageItems"
                            },
                            {
                                label: <Link to={'/management'}>User Management</Link>,
                                key: "management"
                            },
                            {
                                label: commonObj?.name ? commonObj.name : 'You',
                                // key: 'profile',
                                children: [
                                    {
                                        key: 'profile',
                                        label: <Link to={'/profile'}>Profile</Link>,
                                    },
                                    {
                                        key: 'changePassword',
                                        label: <Link to={'/changePassword'}>Change-Password</Link>,
                                    },
                                    {
                                        key: 'Logout',
                                        danger: true,
                                        label: 'Logout',
                                        onClick: () => { window.localStorage.clear(); window.location.reload(); }
                                    }
                                ]
                            }
                        ]}>
                        </Menu>
                    </Layout.Header>

                    <Layout.Content>
                        <Card style={{ borderRadius: "none" }}>
                            <Routes>
                                <Route path="specialization" element={<Type />} >    </Route>
                                <Route path="offer" element={<Offer />}>  </Route>
                                <Route path="coupon" element={<Coupon />}> </Route>
                                <Route path="banner" element={<Banner />} />
                                <Route path="application" element={<Application />} />
                                <Route path="category" element={<Category />} />
                                <Route path="management" element={<UserManagement />} />
                                <Route path="homepageitems" element={<HomePageItems />} />
                                <Route path="add-new-product" element={<Product />} />
                                <Route path="addOn" element={<AddOn />} />
                                <Route path="order" element={<Order />} />
                                <Route path="products" element={<Products />} />
                                <Route path="delivery" element={<Delivery />} />
                                <Route path="deliveryBoy" element={<DeliveryBoy />} />
                                <Route path="settings" element={<Settings />} />
                                <Route path="profile" element={<Profile />} />
                                <Route path="changePassword" element={<ChangePassword />} />
                                <Route path={'*'} element={<Error404 />} />
                            </Routes>
                        </Card>
                    </Layout.Content>
                </Layout>
            </Layout>
        </>
    );

}